.Navigator {
  position: fixed;
  width: 100%;
  padding: 20px 0px;
  z-index: 100;
  -webkit-transition: 0.8s cubic-bezier(0.2, 0.8, 0.2, 1);
  -o-transition: 0.8s cubic-bezier(0.2, 0.8, 0.2, 1);
  transition: 0.8s cubic-bezier(0.2, 0.8, 0.2, 1)
}

.NavigatorScrolled {
  background: rgba(0, 0, 0, 0.8);
  padding: 2px 0px;
  -webkit-backdrop-filter: blur(20px);
          backdrop-filter: blur(20px);
}

.NavigatorGroup {
  max-width: 800px;
  margin: 0 auto;
  display: grid;
  grid-template-columns: repeat(5,auto);
  justify-items: center;
  -ms-flex-align: center;
      align-items: center;
}

.Navigator p {
  color: white;
  font-weight: 700;
  cursor: pointer;
  -webkit-transition: 0.5s;
  -o-transition: 0.5s;
  transition: 0.5s;
  text-shadow: 0px 6px 8px rgba(0, 0, 0, 0.25)
}

.Navigator p:hover {
  background-color: transparent;
  -webkit-transform: translateY(-3px);
      -ms-transform: translateY(-3px);
          transform: translateY(-3px);
}

.Navigator button:hover {
  background: white;
  -webkit-box-shadow: 0 10px 20px rgba(0, 0, 0, 0.25);
          box-shadow: 0 10px 20px rgba(0, 0, 0, 0.25);
  -webkit-transform: translateY(-3px);
      -ms-transform: translateY(-3px);
          transform: translateY(-3px);
}

.Navigator button {
  padding: 8px 20px;
  font-size: 20px;
  background-color: #56ccf2;
  border: none;
  font-weight: 700;
  border-radius: 10px;
  outline: none;
  cursor: pointer;
  -webkit-transition: 0.5s cubic-bezier(0.2, 0.84, 0.2, 1);
  -o-transition: 0.5s cubic-bezier(0.2, 0.84, 0.2, 1);
  transition: 0.5s cubic-bezier(0.2, 0.84, 0.2, 1);
}

@media(max-width:640px){
  .Navigator {
    padding:15px 0; 
  }
  .NavigatorGroup{
    grid-template-columns: repeat(4,auto);
  }

  .Navigator p:nth-child(4) {
    display: none;
  }
}


