.projects-section {
  display: -ms-inline-flexbox;
  display: inline-flex;
  overflow: hidden;
  width: 100%;
  -ms-flex-direction: column;
      flex-direction: column;
  -ms-flex-pack: center;
      justify-content: center;
  -ms-flex-align: center;
      align-items: center;
  padding: 25px 0px;
  position: relative;
}

.projects-section::after {
  display: block;
  content: ' ';
  position: absolute;
  background:rgba(83, 52, 245,0.2);
  width: 60%;
  height: 1px;
  bottom: 0px;
}

.projects-title {
  margin:0px 0px 35px 0px;
  font-size: 45px;
  font-weight: 200;
}


/* Specific NHK Title */
.project-title-nhk {
  display: -ms-flexbox;
  display: flex;
  -ms-flex-direction: row;
      flex-direction: row;
  -ms-flex-pack: center;
      justify-content: center; 
  -ms-flex-align: center; 
      align-items: center;
}

.project-title-nhk img {
  margin-left: 20px;
  width: 10%;
  height: 100%;
}

/* Specific Todo Title */
.project-title-todo {
  display: -ms-flexbox;
  display: flex;
  -ms-flex-direction: row;
      flex-direction: row;
  -ms-flex-pack: center;
      justify-content: center; 
  -ms-flex-align: center; 
      align-items: center;
}

.project-title-todo img {
  margin-left: 25px;
  width: 17%;
  height: 100%;
  -webkit-box-shadow: 3px 5px 20px 2px rgba(0, 0, 0, 0.25);
          box-shadow: 3px 5px 20px 2px rgba(0, 0, 0, 0.25);
  border-radius: 10px;
  
}

.projects-img-container {
  position: relative;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-pack: center;
      justify-content: center;
  -webkit-transform: translateX(-10%);
      -ms-transform: translateX(-10%);
          transform: translateX(-10%);
}

.projects-img {
  height: 100%;
  width: 50%;
}

/* Project Details */
.project-detail {
  top: 50px;
  right: 10%;
  width: 300px;
  position: absolute;
  background: -webkit-linear-gradient(240deg,#424141 15%,#4e5056 70%,#505b6b 94%);
  background: -o-linear-gradient(240deg,#424141 15%,#4e5056 70%,#505b6b 94%);
  background: linear-gradient(-150deg,#424141 15%,#4e5056 70%,#505b6b 94%);
  border-radius: 10px;
  padding: 20px;
  color: #fefefe;
  -webkit-box-shadow: 0 20px 40px rgba(0, 0, 0, 0.25);
          box-shadow: 0 20px 40px rgba(0, 0, 0, 0.25);
}

.project-detail p, .project-detail article {
  margin: 0px;
  font-weight: 400;
  margin-bottom: 12px;
}

.project-detail article {
  margin: 0px;
  font-weight: 200;
  line-height: 1.3;
  margin-bottom: 12px;
}

.project-detail ul {
  margin: 0px;
  font-weight: 200;
  line-height: 1.4;
}


