.Card {
  width: 300px;
  height: 225px;
  position: relative;
  overflow: hidden;
  border-radius: 20px;
  -webkit-box-shadow: 0 20px 40px rgba(0, 0, 0, 0.25);
          box-shadow: 0 20px 40px rgba(0, 0, 0, 0.25);
  display: grid;
  grid-template-rows: 1fr 1fr;
  -webkit-transition: 0.5s cubic-bezier(0.175, 0.82, 0.165, 1);
  -o-transition: 0.5s cubic-bezier(0.175, 0.82, 0.165, 1);
  transition: 0.5s cubic-bezier(0.175, 0.82, 0.165, 1)
}

.Card:hover {
  -webkit-transform: scale(1.1,1.1);
      -ms-transform: scale(1.1,1.1);
          transform: scale(1.1,1.1);
  -webkit-box-shadow: 0 30px 60px rgba(0, 0, 0, 0.5);
          box-shadow: 0 30px 60px rgba(0, 0, 0, 0.5)
}

.Card:hover img {
  -webkit-transform: translateY(-20px);
      -ms-transform: translateY(-20px);
          transform: translateY(-20px);
}

.Card img {
  position: absolute;
  top:0;
  height: 110%;
  z-index: -1;
  -webkit-transition:0.8s cubic-bezier(0.165, 0.84, 0.44, 1);
  -o-transition:0.8s cubic-bezier(0.165, 0.84, 0.44, 1);
  transition:0.8s cubic-bezier(0.165, 0.84, 0.44, 1);
}

.Card h3 {
  color: white;
  font-size: 30px;
  margin: 20px 0 0 20px;
  width: 190px;
}

.Card p {
  color: rgba(255, 255, 255, 0.8);
  text-transform: uppercase;
  font-weight: 600;
  font-size: 18px;
  -ms-flex-item-align: end;
      align-self: end;
  margin: 0 0 20px 20px;
}


